/* global & resets */

* {
    margin: 0;
    padding: 0; 
    box-sizing: border-box;
    font-family: "Poppins";
    color: var(--charcoal);
    border: none;
}

:root {

    /* colors */
    --darkblue: rgb(27, 49, 87);
    --navy: rgb(22, 33, 51);
    --gold: rgb(187, 135, 54);
    --lightgrey: rgb(145, 143, 143);
    --charcoal: rgb(43, 45, 51);
    --darkblue-light: rgb(45, 71, 119);
    --darkblue-lighter: rgb(68, 99, 156);
    --navy-light: rgb(38, 53, 80);
    --navy-lighter: rgb(63, 80, 111);
    --gold-dark: rgb(153, 108, 38);
    --gold-light: rgb(212, 168, 99);
    --gold-lighter: rgb(239, 212, 152);
    --lightgrey-light: rgb(179, 177, 177);
    --lightgrey-lighter: rgb(214, 212, 212);
    --charcoal-light: rgb(65, 68, 75);
    --charcoal-lighter: rgb(90, 93, 101);
}


