.app-body {
    background-color: var(--navy-lighter);
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.app-card-main {
    background-color: #D9D9D9;
    width: 66.25rem;
    height: 38rem;
    display: flex;
    border-radius: 20px;
    padding: 4rem;
    position: relative;
}

.startmenu-info {
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: left;
}

#tmr-logo {
    position: absolute;
    top: 4rem;
    left: 4rem;
}

.left-content {
    width: 50%;
    height: 100%;
    padding: 0.5rem;
}

.right-content {
    width: 50%;
    height: 100%;
    padding: 0.5rem;
    position: relative;
}

.action-button {
    position: absolute;
    bottom: 0rem;
    left: 0rem;
    right: 0rem;
    margin: auto;
}

/* Start Menu */

.startmenu-info, .startmenu-header {
    width: 50%;
    height: 100%;
}

.startmenu-header h1, .quiz-question {
    font-size: 3rem;
    text-align: start;
    margin-top: 6rem;
}

.startmenu-info {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.startmenu-info p {
    text-align: center;
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.startmenu-info p:last-of-type {
    margin-bottom: 4rem;
}

.action-button {
    background-color: var(--gold);
    width: 22rem;
    height: 4rem;
    border: none;
    border-radius: 10px;
    font-size: 2rem;
    color: white;
    box-shadow: var(--charcoal-lighter) 2px 2px;
}

.action-button:hover {
    background-color: var(--gold-light);
}

.action-button:active {
    transform: translate(2px, 2px);
    box-shadow: none;
}


/* Quiz Selection Menu */

#quiz-type-selection {
    display: flex;
    justify-content: space-evenly;
}

.half-width-button {
    width: 13rem;
    height: 3rem;
    border-radius: 10px;
    background-color: var(--navy);
    font-size: 1rem;
    color: white;
}

.half-width-button:hover {
  background-color: var(--navy-lighter);
}

.half-width-button:active {
  background-color: var(--navy-lighter);
}

.selection-buttons p {
    margin-top: 1rem;
    font-style: italic;
}

